.footer {
  padding: 2rem 0;
  background-color: var(--background);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.container {
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 1rem;
}

.topSection {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 768px) {
  .topSection {
    flex-direction: row;
  }
}

.profile {
  margin-bottom: 1rem;
}

.name {
  font-weight: 500;
  font-size: 1.125rem;
  background: linear-gradient(to right, #6EE7B7, #3B82F6);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.title {
  font-size: 0.875rem;
  color: var(--muted-foreground);
}

.socials {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.icon {
  color: var(--muted-foreground);
  transition: color 0.2s ease;
  cursor: pointer;
}

.icon:hover {
  color: var(--primary);
}

.bottomText {
  margin-top: 1.5rem;
  text-align: center;
  font-size: 0.875rem;
  color: var(--muted-foreground);
}
